.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#table, td {
  color : white;
  padding: 15px;
  width: 25%;
}

#th{
  padding: 15px;
}

.pageAnimation:hover{
  background-color: slategray;
  border-radius: 10px;
  width: 25px;
  height: 25px;
  color: white;
  animation: anim;
  animation-duration: 1s;
}

.pageAnimation{
  padding-left: 9px;
}